.display1{
  font-size: 56px;
  line-height: 118%;
}

.display2{
  font-size: 40px;
  line-height: 137%;
}

.display3{
  font-size: 18px;
  line-height: 133%;
}

.big-text{
  font-size: 21px;
  line-height: 165%;
}

.medium-text{
  font-size: 16px;
  line-height: 150%;
}

.small-text{
  font-size: 14px;
  line-height: 17px;
}

.medium-weight{
  font-weight: 500;
}

.body-text{
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 10px;
    }
  }
}

.mlist{
  li{
    line-height: inherit;
    display: flex;
    &::before{
      content: '';
      display: block;
      width: 4px;
      height: 4px;
      border-radius: 50%;
      background-color: #000;
      transform: translateY(8px);
      margin-right: 5px;
      flex-shrink: 0;
    }
    &:not(:last-child){
      margin-bottom: 16px;
    }
  }
}


@media screen and (max-width: 992px){
  .display1{
    font-size: 37px;
  }

  .display2{
    font-size: 28px;
  }

  .big-text{
    font-size: 18px;
  }
}
