.logo{
  width: 140px;
  flex-shrink: 0;
  display: block;
  img{
    width: 100%;
    display: block;
  }
}

.header{
  padding: 20px 0;
  border-bottom: 1px solid rgba($black,.08);
  background-color: #fff;
  position: relative;
  &__inner{
    display: flex;
    align-items: center;
  }
  &__mob{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-grow: 1;
  }
  &__logo{
    margin-right: 57px;
  }
  &__city{
    position: relative;
  }
  &__buttons{
    display: flex;
    align-items: center;
  }
  &__btn{
    padding: 0 28px;
    margin-left: 10px;
  }
  &__burger,
  &__socials{
    display: none;
  }
}

.nav{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    padding: 7px 0;
    border:none;
    &__inner{
      padding: 0 16px;
      height: 48px;
      justify-content: space-between;
      background-color: $grayBg;
      border-radius: 8px;
    }
    &__mob{
      position: fixed;
      left: -200%;
      top: 62px;
      height: calc(100% - 62px);
      width: 100%;
      overflow-y: auto;
      display: flex;
      flex-direction: column;
      justify-content: unset;
      background-color: #fff;
      z-index: 40;
      padding: 20px 0 45px;
      transition: all .4s;
    }
    &__burger{
      width: 18px;
      height: 14px;
      display: flex;
      align-items: center;
      position: relative;
      z-index: 15;
      span{
        display: block;
        width: 100%;
        height: 2px;
        border-radius: 1px;
        background: $black;
        left: 0;
        transition: all .3s;
        &:nth-child(1){
          position: absolute;
          top: 0;
        }
        &:nth-child(3){
          position: absolute;
          bottom: 0;
        }
      }
    }
    &__buttons{
      display: block;
      margin-top: 16px;
    }
    &__btn{
      width: 215px;
      height: 48px;
      max-width: 100%;
      margin: 4px auto 0;
    }
    &__socials{
      padding-top: 40px;
      margin-top: auto;
      display: block;
      text-align: center;
      &-title{
        font-size: 20px;
        margin-bottom: 15px;
      }
      &-desc{
        margin-top: 10px;
      }
    }
    &._open-menu{
      .header__mob{
        left: 0;
      }
      .header__burger{
        span{
          &:nth-child(1){
            transform: rotate(-45deg) translate(-3.5px,5px);
          }
          &:nth-child(2){
            transform: scale(0);
          }
          &:nth-child(3){
            transform: rotate(45deg) translate(-3.5px,-5px);
          }
        }
      }
    }
  }

  .nav{
    margin-top: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
    &__link{
      &:not(:last-child){
        margin-bottom: 15px;
      }
    }
  }
}
