@import "../mixins";
.podbor{
  padding: 40px 0 80px;
  overflow: hidden;
  &__header {
    .modal__top {
      margin-bottom: 0;
    }
  }
  &__top{
    padding: 0 40px;
    display: none;
  }
  &__progress{
    position: relative;
    height: 6px;
    width: 100%;
    background-color: $grayBg;
    border-radius: 4px;
    overflow: hidden;
    &-fill{
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      background-color: $red;
      border-radius: 4px;
      transition: all .3s;
    }
  }
  &__nums{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 14px;
  }
  &__percent{
    &::after{
      content: '%';
    }
  }
  &__count{
    border-bottom: 1px solid $black;
    line-height: 100%;
    transition: all .3s;
    display: none;
    .odometer-formatting-mark{
      display: none;
    }
    &--active{
      display: block;
    }
  }
  &__content{
    margin-top: 12px;
  }
  &__step{
    display: none;
    &--active{
      display: block;
    }
  }
  &__title{
    text-align: center;
  }
  &__desc{
    margin: 15px auto 0;
    text-align: center;
  }
  &__form{
    margin-top: 35px;
  }
  &__nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 120px auto 0;
    max-width: 750px;
  }
  &__btn{
    padding: 0 24px;
    height: 48px;
    min-width: 133px;
    &._prev-step{
      svg{
        margin-right: 21px;
      }
    }
    &._next-step{
      svg{
        margin-left: 21px;
      }
    }
  }
}

.quiz,
.places,
.save{
  display: none;
}

._show{
  display: block;
}

.podbor-radios{
  max-width: 320px;
  margin: 0 auto;
  &__item{
    height: 56px;
    position: relative;
    &:not(:last-child){
      margin-bottom: 18px;
    }
    input{
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      cursor: pointer;
      &:checked+label,
      &:hover+label{
        border-color: $black;
      }
      &:checked+label{
        color: #fff;
        background-color: $black;
      }
    }
    label{
      border: 1px solid rgba($black, .05);
      width: 100%;
      height: 100%;
      border-radius: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: all .3s;
    }
  }
}

.search{
  max-width: 750px;
  margin: 0 auto;
}

.podbor-checks{
  margin-top: 40px;
  max-height: 410px;
  overflow-y: auto;
  @include scroll;
  &__item{
    &:not(:last-child){
      margin-bottom: 16px;
    }
  }
}

.places{
  &__tabs{
    display: flex;
    justify-content: flex-end;
    margin-top: 95px;
    &-wrap{
      display: flex;
      align-items: center;
      padding: 3px;
      background-color: #F7F7F7;
      border-radius: 40px;
    }
  }
  &__tab{
    width: 50px;
    height: 36px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 40px;
    cursor: pointer;
    transition: all .3s;
    &:hover,
    &._active{
      background-color: #fff;
    }
    &._active{
      pointer-events: none;
    }
    &-content{
      display: none;
      &._active{
        display: block;
      }
    }
  }
  &__content{
    margin-top: 34px;
  }
  .best__slider{
    margin: 0;
  }
  &__nav{
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: 70px;
  }
  &__btn{
    margin-top: 18px;
    padding: 0 42px;
    height: 48px;
    &._first-step{
      svg{
        display: none;
      }
    }
  }
}

.map{
  height: 700px;
  width: 100%;
  .ymaps-2-1-79-balloon__layout{
    background-color: transparent;

  }
  .ymaps-2-1-79-balloon{
    box-shadow: none;
  }
  .ymaps-2-1-79-balloon__close-button{
    opacity: 1;
  }
  .place-card{
    display: block;
    border-radius: 16px;
    box-shadow: 0 2px 20px rgba($black,.05);
    &__content{
      padding: 12px 16px 20px;
    }
  }
  //.ymaps-2-1-79-map ymaps:after {
  //  //display: none;
  //}
  .ymaps-2-1-79-balloon__tail:after{
    display: none;
  }
  .ymaps-2-1-79-balloon__close+.ymaps-2-1-79-balloon__content{
    padding: 0;
    margin-top: 30px;
  }
}

.save{
  &__copy{
    margin: 29px auto 0;
    max-width: 400px;
  }
  &__socials{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 55px;
    &-item{
      margin: 0 8px;
      &:hover{
        svg{
          transform: translateY(-5px);
        }
      }
    }
  }
}


@media screen and (max-width: 992px){
  .podbor{
    padding: 10px 0 40px;
    position: relative;
    &__top{
      padding: 0;
    }
    &__content{
      margin-top: 30px;
    }
    &__desc{
      margin-top: 10px;
      br{
        display: none;
      }
    }
    &__form{
      margin-top: 20px;
    }
    &__nav{
      margin-top: 50px;
    }
  }

  .podbor-checks{
    max-height: 300px;
  }

  .places{
    &__tabs,
    &__content{
      margin-top: 30px;
    }
    &__content{
      padding-bottom: 108px;
    }
    &__nav{
      margin: 0;
      width: 100%;
      background-color: #fff;
      position: absolute;
      left: 0;
      bottom: 2px;
      flex-direction: row-reverse;
      padding: 16px;
      box-shadow: 2px 0 6px rgba($black,.16);

    }
    &__btn{
      margin: 0;
      flex-grow: 1;
      &._first-step{
        flex-grow: unset;
        width: 48px;
        padding: 0;
        margin-right: 16px;
        span{
          display: none;
        }
        svg{
          display: unset;
        }
      }
    }
  }

  .save{
    &__socials{
      margin-top: 20px;
    }
    &__copy{
      margin-top: 40px;
    }
  }
}
