.auth{
  height: 100%;
  &__desc{
    margin: 26px auto 0;
    text-align: center;
    max-width: 415px;
  }
  &__form{
    margin: 26px auto 0;
    width: 332px;
    max-width: 100%;
    &-buttons{
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 30px;
    }
    &-btn{
      width: 100%;
      height: 56px;
      margin-top: 16px;
      &._telegram{
        svg{
          margin-right: 9px;
        }
      }
    }
    &-forgot{
      margin-top: 30px;
      color: #6B6B6B;
      &:hover{
        color: $primary;
      }
    }
    &-hint{
      line-height: 23px;
      margin-top: 24px;
      color: #6B6B6B;
      text-align: center;
      a{
        text-decoration: underline;
      }
    }
  }
  &__telegram{
    margin: 2px auto 0;
    width: 670px;
    max-width: 100%;
    img{
      width: 100%;
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .auth{
    display: flex;
    flex-direction: column;
    &__desc{
      margin-top: 12px;
    }
    &__form{
      width: 100%;
      margin-top: 35px;
      flex-grow: 1;
      display: flex;
      flex-direction: column;
      &-buttons{
        padding-top: 15px;
        margin-top: auto;
      }
    }
  }
}
