.footer{
  &__logo{
    margin-right: 75px;
  }
  &__links{
    column-count: 2;
    column-gap: 16px;
  }
  &__link{
    font-size: 16px;
    line-height: 19px;
    display: block;
    min-width: 178px;
    &:not(:last-child){
      margin-bottom: 15px;
    }
  }
  &__links2,
  &__social{
    display: flex;
    align-items: center;
  }
  &__social-item,
  &__link2{
    font-size: 16px;
    line-height: 19px;
    &:not(:last-child){
      margin-right: 68px;
    }
  }
  &__social-item{
    display: flex;
    align-items: center;
    svg{
      margin-right: 8px;
    }
  }
}


.footer-top{
  padding: 40px 0;
  background-color: $dark;
  color: #fff;
  &__inner{
    display: flex;
  }
}

.footer-bottom{
  padding: 20px 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}


@media screen and (max-width: 992px){
  .footer{
    &__links{
      margin-top: 25px;
      column-count: 1;
    }
    &__links2{
      color: #fff;
      padding: 16px 0 26px 16px;
      width: calc(100% + 32px);
      margin-left: -16px;
      background-color: $dark;
      display: block;
    }
    &__link2{
      display: block;
      &:not(:last-child){
        margin-bottom: 16px;
      }
    }
    &__social{
      padding: 20px 0;
      justify-content: space-between;
      flex-wrap: wrap;
      margin-top: -10px;
      &-item{
        margin-top: 10px;
        white-space: nowrap;
        &:not(:last-child){
          margin-right: 10px;
        }
      }
    }
  }

  .footer-top{
    padding: 24px 0 0;
    &__inner{
      display: block;
    }
  }

  .footer-bottom{
    padding: 0;
    &__inner{
      display: block;
    }
  }
}
