.place-card,
.direction-card{
  display: block;
  &:hover{
    color: inherit;
    .place-card__img,
    .direction-card__img{
      img{
        transform: scale(1.1);
      }
    }
  }
  &__img{
    position: relative;
    overflow: hidden;
    border-radius: 16px;
    img{
      transition: all .3s;
      position: absolute;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

.place-card{
  display: flex;
  flex-direction: column;
  &__img{
    padding-bottom: 74.5%;
  }
  &__mark{
    top: 16px;
    left: 16px;
    position: absolute;
    padding: 4px 7px;
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 32px;
    font-size: 13px;
    color: #fff;
    background-color: $dark;
    border-radius: 4px;
  }
  &__title{
    margin-top: 10px;
    max-width: 270px;
  }
  &__metro{
    padding-top: 10px;
    margin-top: auto;
    font-size: 15px;
    display: flex;
    align-items: center;
    &-icon{
      width: 10px;
      height: 10px;
      border-radius: 50%;
      display: block;
      flex-shrink: 0;
      margin-right: 6px;
    }
  }
}

.direction-card{
  &__img{
    padding-bottom: 128%;
  }
  &__title{
    margin-top: 10px;
  }
}
