.personal{
  height: 100%;
  text-align: center;
  display: flex;
  flex-direction: column;
  &__img{
    margin: 0 auto 27px;
  }
  &__links{
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: 27px;
    flex-grow: 1;
  }
  &__link{
    &:not(:last-child){
      margin-bottom: 20px;
    }
  }
  &__logout{
    color: $primary;
    &:hover{
      color: $black;
    }
  }
}


@media screen and (max-width: 992px){
  .personal{
    &__links{
      margin-top: 18px;
    }
    &__link{
      &:not(:last-child){
        margin-bottom: 10px;
      }
    }
    &__logout{
      margin-top: auto;
      color: #fff;
      background-color: $primary;
      width: 100%;
      height: 56px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 8px;
      font-size: 16px;
      font-weight: 500;
      &:hover{
        background-color: darken($primary,10%);
        color: #fff;
      }
    }
  }
}
