@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import 'components/_fonts.scss';
._red{
    background-color: $red;
}

._blue{
    background-color: $blue;
}

._gray{
    background-color: #919191;
}

.form-group {
    position: relative;
    margin: 0 0 16px;
    label{
        position: absolute;
        left: 20.5px;
        top: 20px;
        color: #8E8E8E;
        font-size: 16px;
        transition: all .3s;
        pointer-events: none;
    }

    input,
    textarea{
        border-radius: 8px;
        border: 1px solid #D7D7D7;
        height: 56px;
        width: 100%;
        font-size: 16px;
        background-color: transparent;
        color: $black;
        padding: 0 20.5px;
    }
    textarea:focus,
    input:focus,
    .input-active{
        padding-top: 16px;
        &+label{
            top: 4px;
            font-size: 12px;
            line-height: 20px;
        }
    }
    textarea{
        padding: 22px 0;
        height: 230px;
        resize: none;
    }
}

.img-upload{
    width: 200px;
    height: 200px;
    position: relative;
    input{
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: 2;
        opacity: 0;
        cursor: pointer;
    }
    label{
        display: flex;
        height: 100%;
        width: 100%;
        background-color: $grayBg;
        background-image: url(../img/camera.svg);
        background-repeat: no-repeat;
        background-position: center;
        border-radius: 50%;
    }
    &__result{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        z-index: 3;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%,-50%);
        }
    }
    &__remove{
        position: absolute;
        left: 50%;
        bottom: 10px;
        transform: translateX(-50%);
        background-color: rgba($black,.5);
        border-radius: 10px;
        &:hover{
            background-color: rgba($black,.8);
        }
    }
}

.check {
    position: relative;
    input {
        position: absolute;
        opacity: 0;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        z-index: 2;
        cursor: pointer;
        &:checked+label {
            &::before {
                background-image: url(../img/check.svg);
                background-color: $dark;
                border-color: $dark;
            }
        }
        &:disabled+label {
            color: #AAAAAA;
            pointer-events: none;
        }
    }
    label {
        padding-left: 36px;
        position: relative;
        min-height: 22px;
        font-size: 18px;
        line-height: 22px;
        cursor: pointer;
        &:before {
            content: '';
            position: absolute;
            z-index: 1;
            border-radius: 4px;
            border: 1px solid #D7D7D7;
            width: 24px;
            height: 24px;
            left: 0;
            top: -1px;
            transition: all .100s;
            background-repeat: no-repeat;
            background-position: center;
        }
    }
}

.copy-text{
    border-radius: 8px;
    border: 1px solid #D7D7D7;
    height: 56px;
    font-size: 16px;
    padding: 0 45px 0 20px;
    display: flex;
    align-items: center;
    position: relative;
    &__value{
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
    &__btn{
        position: absolute;
        right: 16px;
        top: 50%;
        transform: translateY(-50%);
        &:hover{
            path{
                fill: $black;
            }
        }
    }
    &__alert{
        position: absolute;
        bottom: 100%;
        right: 0;
        margin-bottom: 4px;
        padding: 4px 7px;
        display: flex;
        align-items: center;
        justify-content: center;
        min-width: 32px;
        font-size: 13px;
        color: #fff;
        background-color: $dark;
        border-radius: 4px;
        opacity: 0;
        pointer-events: none;
        transition: all .3s;
    }
    &--active{
        .copy-text__alert{
            opacity: 1;
        }
    }
}
