@import '../vars';
.modal {
    background: #fff;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    z-index: 100;
    opacity: 0;
    pointer-events: none;
    transition: all .2s;
    &--active{
        opacity: 1;
        pointer-events: unset;
    }
    &__top{
        margin: 40px 0;
        background-color: $grayBg;
        height: 71px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 40px;
        border-radius: 16px;
        &-right{
            width: 140px;
            display: flex;
            justify-content: flex-end;
            align-items: center;
        }
    }
    &__close{
        &:hover{
            path{
                fill: $primary;
            }
        }
    }
    &__scroll{
        padding: 60px 0;
        overflow-y: auto;
        height: calc(100% - 150px);
        .container{
            height: 100%;
        }
    }
    &__title{
        text-align: center;
    }

}


@media screen and (max-width: 992px){
    .modal{
        &__top{
            margin: 7px 0;
            height: 48px;
            padding: 0 16px;
            &-right{
                width: unset;
            }
            &-title{
                display: none;
            }
        }
        &__scroll{
            padding: 22px 0;
            height: calc(100% - 63px);
        }
    }
}


