.city{
  &__toggle{
    display: flex;
    align-items: center;
    &:hover{
      cursor: pointer;
      color: $primary;
      path{
        stroke: $primary;
      }
    }
    svg{
      margin-left: 6px;
    }
  }
}


.city-ask{
  z-index: 11;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate(-50%,14px);
  width: 316px;
  background-color: rgba($black,.6);
  text-align: center;
  color: #fff;
  border-radius: 16px;
  overflow: hidden;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  &__top{
    padding: 16px 16px 25px;
  }
  &__desc{
    color: $secondary;
  }
  &__bottom{
    display: grid;
    grid-template-columns: 1fr 1fr;
    border-top: 1px solid #7D7D7D;
  }
  ._change-city{
    color: $secondary;
    &:hover{
      color: #fff;
    }
  }
  ._confirm-city{
    border-radius: 0;
  }
}

.city-select{
  &__items{
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: 55px;
  }
  &__item{
    &:not(:last-child){
      margin-bottom: 20px;
    }
    &:hover{
      color: $primary;
      cursor: pointer;
    }
  }
}


@media screen and (max-width: 992px){
  .city{
    &__toggle{
      max-width: 100%;
      width: 215px;
      height: 48px;
      border-radius: 8px;
      margin: 0 auto;
      justify-content: center;
      border: 1px solid $black;
      font-size: 16px;
      font-weight: 500;
      svg{
        margin-left: 9px;
      }
    }
  }

  .city-ask{
    display: none;
  }
}
