.main{
  padding: 180px 0 230px;
  background-image: url(../img/main-bg.webp);
  background-position: center bottom;
  background-size: cover;
  background-position: center;
  &__inner{
    max-width: 685px;
  }
  &__title{
    min-height: 133px;
    br{
      display: none;
    }
  }
  &__desc{
    margin-top: 19px;
    max-width: 570px;
  }
  &__buttons{
    margin-top: 37px;
    display: flex;
    align-items: center;
  }
  &__btn{
    padding: 0 26px;
    height: 56px;
    margin-right: 24px;
    min-width: 212px;
    &-play{
      margin-left: 16px;
      width: 32px;
      height: 32px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      background-color: rgba(#fff,.25);
    }
  }
}

.best{
  padding: 100px 0;
  background-color: $dark;
  color: #fff;
  overflow: hidden;
  &__top{
    position: relative;
  }
  &__title{
    max-width: 635px;
    position: relative;
    z-index: 2;
  }
  &__desc{
    max-width: 1002px;
    margin-top: 25px;
    position: relative;
    z-index: 2;
  }
  &__img{
    position: absolute;
    right: 0;
    top: 0;
    pointer-events: none;
  }
  &__slider{
    margin-top: 57px;
    .swiper{
      &-wrapper{
        align-items: stretch;
      }
      &-slide{
        height: auto;
        width: 320px;
        max-width: 100%;
      }
    }
  }
  &__bottom{
    margin-top: 60px;
    display: flex;
  }
}

.direction{
  padding: 100px 0;
  overflow: hidden;
  &__desc{
    max-width: 1016px;
    margin-top: 25px;
  }
  &__slider{
    margin-top: 57px;
    position: relative;
    .swiper{
      &-container{
        @media screen and (min-width: 1430px) {
          overflow: hidden;
        }
      }
      &-slide{
        width: 250px;
        max-width: 100%;
      }
      &-button-next{
        right: unset;
        left: 100%;
        transform: translateX(-50%);
      }
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 53px 0 64px;
    background-image: url(../img/main-bg2.webp);
    &__title{
      max-width: 460px;
      br{
        display: unset;
      }
    }
    &__desc{
      max-width: 460px;
      margin-top: 24px;
    }
    &__buttons{
      margin-top: 54px;
      flex-direction: column;
    }
    &__btn{
      margin: 16px auto 0 0;
      height: 50px;
      padding: 0 30px;
      min-width: unset;
      font-size: 16px;
      &-play{
        margin-left: 14px;
      }
    }
  }

  .best{
    padding: 40px 0;
    &__top{
      padding-bottom: 50px;
    }
    &__img{
      width: 112px;
      bottom: 0;
      top: unset;
    }
    &__slider{
      margin-top: 26px;
    }
    &__bottom{
      margin-top: 90px;
    }
  }

  .direction{
    padding: 50px 0;
    &__slider{
      margin-top: 32px;
    }
  }
}
