@import "../vars";
.mbtn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.mbtn-black{
    height: 40px;
    color: #fff;
    background-color: $black;
    border-radius: 8px;
    &:hover{
        background-color: lighten($black,10%);
        color: #fff;
    }
}

.mbtn-primary{
    height: 40px;
    color: #fff;
    background-color: $primary;
    border-radius: 8px;
    &:hover{
        background-color: darken($primary,10%);
        color: #fff;
    }
}

.mbtn-transparent{
    height: 40px;
    &:hover{
        color: $primary;
    }
}

.mbtn-gray-outline{
    border-radius: 8px;
    border: 1px solid rgba($black,.1);
    color: $black;
    &:hover{
        background-color: rgba($black,.2);
        border-color: rgba($black,.2);
        color: $black;
    }
}

.arrow-link{
    display: flex;
    align-items: center;
    &:hover{
        color: $primary;
        .arrow-link__round{
            background-color: $primary;
        }
    }
    &__round{
        margin-left: 20px;
        flex-shrink: 0;
        width: 40px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #fff;
        border-radius: 50%;
    }
}

.swiper-button-next{
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    background-color: #fff;
    box-shadow: 2px 4px 7px rgba(#323242,.09);
    &.swiper-button-disabled{
        opacity: 1;
    }
}
